.dohod {
  padding: 200px 0;

  &__container {
      text-align: center;
      margin-bottom: 100px;
  }

  &__title {
      font-family: "Proxima Nova";
      font-size: 60px;
      font-weight: 700;
      color: #403f3f;
      letter-spacing: 3px;
      display: inline-block;
      position: relative;
      z-index: 10;

      @media(max-width: 1024px) {
          font-size: 30px;
      }
  }

  &__bg{
      width: 580px;
      height: 20px;
      background-color: #87d04c;
      position: relative;
      top: -37px;
      margin: 0 auto;
      transform: translateX(-33%);

      @media(max-width: 1024px) {
          display: none;
      }
  }
}