.offer{
      
    padding-bottom: 200px;

    &__container {
        text-align: center;
        margin-bottom: 100px;
    }

    &__title {
        font-family: "Proxima Nova";
        font-size: 60px;
        font-weight: 700;
        color: #403f3f;
        letter-spacing: 3px;
        display: inline-block;
        position: relative;
        z-index: 10;

        @media(max-width: 1024px) {
            font-size: 30px;
        }
    }

    &__bg{
        width: 450px;
        height: 20px;
        background-color: #87d04c;
        position: relative;
        top: -37px;
        margin: 0 auto;
        transform: translateX(-23%);

        @media(max-width: 1024px) {
            display: none;
        }
    }

    &__wrapper {
        display: flex;
        justify-content: space-between;

        @media(max-width:1700px) {
            justify-content: space-around;
        }


        @media(max-width: 660px) {
            display: flex;
            flex-direction: column;
            align-items: center;
        }

        &-line {
          &::after {
            content: url(../img/line.png);
            position: absolute;
            top: 50%;
            margin-top: -12px;
            transform: translateX(377px);

            @media(max-width: 1200px){
                transform: translateX(150px) scale( .5);
            }

            @media(max-width: 660px) {
                display: none;
            }
            
          }
        }

        &-item {

            width: 340px;
            height: 340px;
            background: #60bee1;
            border-radius: 50%;
            padding-top: 50px;
            margin-bottom: 20px;
            box-shadow: 0 0 10px rgba(0,0,0, .5);
            position: relative;
            z-index: 3;

            @media(max-width: 1200px) {
                width: 200px;
                height: 200px;
                padding-top: 5px;
            }

            @media(max-width: 6501200px) {
                margin-bottom: 65px;
            }
            /* @media(max-width: 768px) {
                width: 33%;
            }

            @media(max-width: 550px) {
                width: 90%;
                margin-bottom: 10px;
            } */
        }

        &-icon {
            margin: 0 auto;

            @media(max-width: 1200px){
                transform: scale( .7);
            }

            /* @media(max-width: 1024px){
                transform: scale( .4);
            } */
        }

        &-icon-key{
            background-image: url('../img/svg/icon_key.png');
            background-repeat: no-repeat;
            background-position: center;
            width: 86px;
            height: 86px;
            margin-bottom: 25px;

            @media(max-width: 1200px){
                margin-bottom: 0px;
            }
        }

        &-icon-bag{
            background-image: url('../img/svg/icon_bag.png');
            background-repeat: no-repeat;
            background-position: center;
            max-width: 260px;
            width: 80px;
            height: 96px;
        }

        &-icon-hammer {
            background-image: url('../img/svg/icon_hammer.png');
            background-repeat: no-repeat;
            background-position: center;
            width: 108px;
            height: 108px;
        }

        &-disc {
            font-family: "Proxima Nova - Extrabld";
            font-size: 30px;
            font-weight: 700;
            color: white;
            letter-spacing: 1.5px;
            text-align: center;
            padding-top: 5px;

            @media(max-width: 1200px){
                font-size: 18px;
            }

            @media(max-width: 1024px){
                padding-top: 0px;
            }
        }
    }

}

.offers {
    position: relative;

      &-int {
        background-image: url(../img/1.png);
        background-repeat: no-repeat;
        background-position: center;
        position: absolute;
        width: 104px;
        height: 162px;
        top: -60px;
        left: -50px;
        
        @media(max-width: 1200px) {
          transform: scale(.7);
        }
    }

    &-two {
      background-image: url(../img/2.png);
      background-repeat: no-repeat;
      background-position: center;
      position: absolute;
      width: 140px;
      height: 164px;
      top: -60px;
      left: -50px;

      @media(max-width: 1200px) {
        transform: scale(.7);
      }
  }

  &-three {
    background-image: url(../img/3.png);
    background-repeat: no-repeat;
    background-position: center;
    position: absolute;
    width: 141px;
    height: 167px;
    top: -60px;
    left: -50px;

    @media(max-width: 1200px) {
      transform: scale(.7);
    }
  }
}
