.topline {
    
    &__bg {
        background: var(--white);
    }

    &__row {
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-wrap: wrap;
        padding-top: 25px;
        padding-bottom: 10px;
    }
    
    &__logo {
        background-image: url(../img/svg/logo.svg);
        background-repeat: no-repeat;
        width: 169px;
        height: 169px;
        background-size: cover;

        @media(max-width: 768px) {
            width: 100px;
            height: 100px; 
        }
        @media(max-width: 425px) {
            display: none; 
        }
    }

    &__burger {
        display: flex;

        @media(max-width: 1550px) {
            cursor: pointer;
        }

        &-img {
            background-image: url(../img/svg/burger.svg);
            background-repeat: no-repeat;
            background-size: cover;
            width: 90px;
            height: 50px;   
            margin-right: 35px;

            @media(max-width: 1200px) {
                width: 70px;
                height: 40px; 
                margin-right: 20px;
            }
        }

        &-name {
            color: #5c5b5b;
            font-family: "Proxima Nova";
            font-size: 30px;
            font-weight: 700;
            text-transform: uppercase;

            @media(max-width: 768px) {
                display: none; 
            }
        }
    }

    &__menu{

        @media(max-width: 1550px) {
            display: none;
        }

        &-item {
            color: #5c5b5b;
            font-family: "Proxima Nova";
            font-size: 26px;
            font-weight: 700;
            text-transform: uppercase;
            text-decoration-line: none;
            transition: all .3s;

            &:hover {
                color: #1fca16;
                border-bottom: 2px solid #1fca16;
                padding-bottom: 10px;
            }
        }

        &-line {
            margin-right: 25px;

            &::after {

                content: "";
                width: 4px;
                height: 20px;
                background: #1591bf;
                display: inline-block;
                transform: translateX(15px);

            }
        }
    }

    &__phone {
        background-color: #1fca16;
        padding: 20px 30px;
        border-radius: 40px;
        transition: all .3s;

        &:hover {
            box-shadow: 0 0 10px rgba(0,0,0,.5);
        }

        &-number {
            font-family: "Proxima Nova";
            font-size: 30px;
            font-weight: 700;
            text-transform: uppercase;
            color: var(--white);
            text-decoration-line: none;

            @media(max-width: 768px) {
                font-size: 16px;
            }
        }

    }

}

.menu__mobile {
    display: none;

    ul {
        text-align: center;
        list-style-type: none;
        margin: 0;
        padding: 0;
    }

    li {
        border-top: 1px solid green;
        padding: 10px 0;
    }
    
    li:last-child {
        border-bottom: 1px solid green;
    }


    a {
        color: #5c5b5b;
        font-family: "Proxima Nova";
        font-size: 26px;
        font-weight: 700;
        text-transform: uppercase;
        text-decoration-line: none;
        transition: all .3s;

        &:hover {
            color: #1fca16;
            border-left: 2px solid #1fca16;
            border-right: 2px solid #1fca16;
            padding-left: 10px;
            padding-right: 10px;
        }
    }
}

.header {
    background-image: url("../img/header.png");
    background-repeat: no-repeat;
    background-size: cover;
    height: 962px;
    background-position: center;

    &__wrapper {
        width: 50%;

        @media(max-width: 850px){
            width: 100%;
        }
        @media(max-width: 768px){
            margin-top: -110px;
        }
        @media(max-width: 480px){
            margin-top: 45px;
        }
    }

    &__right {
        width: 50%;
        position: relative;
        top: -75px;

        @media(max-width: 768px){
            width: 70%;
            top: -145px;
        }

        @media(max-width: 480px){
            width: 100%;
            top: -90px;
        }

        form {
            background: #2eafdf;
            width: 590px;
            border-radius: 25px;
            text-align: center;
            padding: 40px;
            padding-top: 80px;
            box-shadow: 0 0 10px rgba(0,0,0,.5);
            margin-left: auto;

            @media(max-width: 1250px){
                width: 100%;
            }

            @media(max-width: 1023px){
                padding: 20px;
            }

            input[type="text"] {
                width: 100%;
                height: 90px;
                border-radius: 45px;
                border: none;
                padding: 15px 25px 15px 75px;
                font-family: "Proxima Nova";
                font-size: 30px;
                margin-bottom: 60px;
                outline: none;

                @media(max-width: 1023px){
                    height: 60px;
                    font-size: 18px;
                    margin-bottom: 30px;
                }
            }

             input[type="submit"] {

                opacity: 0;
                width: 1px;
                height: 1px;
                position: absolute;
                /*
                background-color: #1fca16;
                padding: 25px 60px;
                border-radius: 40px;
                transition: all .3s;
                color: white;
                border: none;
                font-family: "Proxima Nova";
                font-size: 24px;
                text-transform: uppercase;
                outline: none;
                cursor: pointer;

                &:hover {
                    box-shadow: 0 0 10px rgba(0,0,0,.5);
                }

                @media(max-width: 1023px){
                    padding: 15px 25px;
                    font-size: 18px;
                }*/


            } 
        }
    }

    &__left {
        border-left: 20px solid #24a71d;
    }

    &__row {
        padding-top: 185px;
        display: flex;

        @media(max-width: 850px){
            flex-direction: column-reverse;
            align-items: center;
        }
    }

    &__btn {
        /* background-color: #2eafdf;
        display: inline-block;
        padding: 23px 100px;
        border-radius: 40px;
        letter-spacing: 3px;
        cursor: pointer;
        margin-left: 50px;

        @media(max-width: 1023px){
            padding: 15px 25px;
            font-size: 18px;
        }

        &:hover {
            box-shadow: 0 0 10px rgba(0,0,0,.5);
        }*/

        a {
            color: #ffffff;
            font-family: "Proxima Nova";
            font-size: 24px;
            font-weight: 700;
            text-transform: uppercase;
            text-decoration-line: none;
            transition: all .3s;

            @media(max-width: 1023px){
                font-size: 18px;
            }


        } 

        padding: 23px 90px;
        border-radius: 40px;
        transition: 1s ease-out;
        color: white;
        border: none;
        font-family: "Proxima Nova";
        font-size: 24px;
        text-transform: uppercase;
        outline: none;
        cursor: pointer;
        display:inline-block;
        overflow:hidden;
        text-transform:capitalize;
        z-index:1;
        position:relative;
        background: #2eafdf;

        border-radius: 40px;
        letter-spacing: 3px;
        cursor: pointer;
        margin-left: 50px;

        @media(max-width: 560px) {
            padding: 15px 20px;
        }

        &:before{
          content:'';
          position:absolute;
          top:-30px;
          left:50%;
          -webkit-transform:translate(-50%, -50%) scale(1);
          transform:translate(-50%, -50%) scale(1);
          display:block;
          width:50px;
          height:50px;
          background: white;
          border-radius:50px;
          -webkit-transition: 0.5s;
          transition: 0.5s;
          z-index:-1;
        }
        &:hover{
          -webkit-box-shadow:0 5px 20px rgba(0,0,0,.5);
          box-shadow:0 5px 20px rgba(0,0,0,.5);
           cursor:pointer;
           -webkit-transition-delay: .1s;
            transition-delay: .1s;
            color: #1fca16;
            z-index:8;

            a {
                color: #2eafdf;
            }

          &:before{
            -webkit-transform:scale(8.3);
            transform:scale(8.3);
          }
        }
    }

    &__left {
        padding: 10px 0 10px 40px;
        margin-bottom: 40px;

        @media(max-width:480px) {
            border-left: 8px solid #24a71d;
            padding: 5px 0 5px 20px;
        }

        &-title {
            font-family: "Proxima Nova - Black";
            color: #ffffff;
            font-size: 72px;
            font-weight: 400;
            text-transform: uppercase;
            letter-spacing: 3px;
            margin-bottom: 15px;

            @media(max-width: 1500px) {
                font-size: 34px;
            }
            @media(max-width: 480px) {
                font-size: 20px;
            }
        }

        &-disc{
            color: #ffffff;
            font-family: "Proxima Nova - Semibold";
            font-size: 60px;
            font-weight: 400;
            text-transform: uppercase;
            letter-spacing: 8px;

            @media(max-width: 1500px) {
                font-size: 30px;
            }
            @media(max-width: 480px) {
                font-size: 16px;
            }
        }

        &-save {
            color: #1da417;
            font-family: "Proxima Nova - Extrabld";
            font-size: 60px;
            font-weight: 400;
            text-transform: uppercase;
            letter-spacing: 6px;

            @media(max-width: 1500px) {
                font-size: 30px;
            }
            @media(max-width: 480px) {
                font-size: 16px;
            }
        }

        &-clock {
            font-family: "Proxima Nova - Extrabld";
            font-size: 24px;
            text-transform: uppercase;
            color: white;
            letter-spacing: 2px;
            max-width: 535px;
            width: 90%;

            @media(max-width: 1500px) {
                font-size: 18px;
            }
            @media(max-width: 480px) {
                font-size: 16px;
            }
            
        }
    }
}

