.akcija {
    background: #eeeeed;
    padding-bottom: 80px;

    &__row {
      padding-top: 150px;
      padding-bottom: 100px;
      position: relative;
    }

    &__title {
        font-family: "Proxima Nova - Extrabld";
        font-size: 60px;
        letter-spacing: 6px;

        @media(max-width: 768px){
          font-size: 22px;
        }
    }

    &__disc {
        font-family: "Proxima Nova - Semibold";
        font-size: 48px;
        letter-spacing: 6px;

        @media(max-width: 768px){
          font-size: 18px;
        }
    }

    &__btn {
      display: flex;
      justify-content: space-around;
      flex-wrap: wrap;
    }

    &__blocks {
      width: 60%;
      margin-left: auto;

      @media(max-width: 1650px) {
        width: 80%;
        margin: 0 auto;
     }
    }
    
    &__block {
      text-align: center;
      margin-bottom: 100px;



    }

    &__img {
      background-image: url(../img/auto.png);
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
      min-height: 483px;
      width: 1058px;
      position: absolute;
      top: -62px;
      left: -311px;

      @media(max-width: 1650px){
        width: 50%;
        /* transform: scale(0.8); */
        top: -260px;
        left: 50%;
        margin-left: -500px;
      }

      @media(max-width: 1200px){
        width: 70%;
      }

      @media(max-width: 1024px) {
        /* position: static; */
        left: 70%;
      }

      @media(max-width: 768px) {
        transform: scale( .4);
        left: 90%;
      }

      @media(max-width: 480px) {
        transform: scale( .3);
        margin-left: -528px;
      }

    }

    &__link {
        padding: 30px 110px;
        color: #ffffff;
        font-family: "Proxima Nova";
        text-transform: uppercase;
        border-radius: 54px;
        cursor: pointer;

        &:hover {
          box-shadow: 0 0 10px rgba(0,0,0,  .5);
        }

        @media(max-width: 768px){
          padding: 15px 50px;
          font-size: 18px;
        }

        &-green {
          background-color: #2cbb25;
          margin-bottom: 20px;

          a {
            color: #ffffff;
            font-family: "Proxima Nova";
            font-size: 30px;
            font-weight: 700;
            text-transform: uppercase;
            text-decoration-line: none;

            @media(max-width: 768px){
              font-size: 18px;
            }
          }
        }

        &-blue {
          background-color: #458fe8;
          margin-bottom: 20px;

          a {
            color: #ffffff;
            font-family: "Proxima Nova";
            font-size: 30px;
            font-weight: 700;
            text-transform: uppercase;
            text-decoration-line: none;

            @media(max-width: 768px){
              font-size: 18px;
            }
          }
        }
    }
}

.akcija__icons {
  display: flex;
  align-items: flex-end;
  justify-content: space-around;

  @media(max-width: 700px){
    flex-direction: column;
    align-items: center;
  }
}


.akcija__icon { 
    &-disc {
      color: #212121;
      font-family: "Proxima Nova - Semibold";
      font-size: 30px;
      text-align: center;
      margin-bottom: 10px;
    }


    &-img{
      margin: 0 auto;
      margin-bottom: 30px;

      &-brill {
          background: url(../img/brill.png);
          width: 126px;
          height: 109px;
      }

      &-money {
        background: url(../img/money.png);
        width: 120px;
        height: 121px;
      }

      &-machin {
        background: url(../img/machin.png);
        width: 127px;
        height: 127px;
      }
  }
}