.work {
  padding: 200px 0;
  margin-top: -265px;
  background: white;
  position: relative;

  &__container {
      text-align: center;
      margin-bottom: 100px;
  }

  &__title {
      font-family: "Proxima Nova";
      font-size: 60px;
      font-weight: 700;
      color: #403f3f;
      letter-spacing: 3px;
      display: inline-block;
      position: relative;
      z-index: 10;

      @media(max-width: 1024px) {
          font-size: 30px;
      }
  }

  &__bg{
      width: 700px;
      height: 20px;
      background-color: #87d04c;
      position: relative;
      top: -37px;
      margin: 0 auto;
      transform: translateX(-40%);

      @media(max-width: 1024px) {
          display: none;
      }
  }

  &__cards {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
  }
}
.work__card {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-bottom: 50px;

    box-shadow: 2px 4px 17px 4px rgba(189, 189, 189, .36);
    border-radius: 5px;
    max-width: 500px;
    width: 30%;
    padding: 50px;
    text-align: center;

    @media(max-width: 1500px) {
      padding: 20px;
    }

    @media(max-width: 1023px) {
      width: 48%;
    }

    @media(max-width: 570px) {
      width: 100%;
    }

    &-btn {
      a {
        color: #272727;
        font-family: "Proxima Nova - Semibold";
        font-size: 24px;
        text-decoration: none;
        padding: 20px 80px 20px 40px;
        border-radius: 40px;
        transition: all .3s;

        @media(max-width: 1500px) {
          font-size: 18px;
          padding: 15px 40px 15px 40px;
        }

        &:hover {
          background-color: #458fe8;
          color: white;
        }

        &:hover .work__card-btn-arr {
          background: url("../img/arr-white.png");
        }
      }

      &-arr {
        background: url("../img/arrow-right.png");
        width: 34px;
        height: 16px;
        display: inline-block;
        transform: translateX(25px);
      }
    }

    &-text {
      margin-bottom: 50px;
      flex-grow: 1;
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      &-disc {
        color: #383838;
        font-family: "Proxima Nova";
        font-size: 24px;

        @media(max-width: 1500px) {
          font-size: 18px;
        }
      }
  
      &-icon {


        &-laim {
          background-image: url("../img/laim.png");
          width: 120px;
          height: 225px;
        }

        &-iq {
          background-image: url("../img/iq.png");
          width: 293px;
          height: 131px;
        }

        &-comandir {
          background-image: url("../img/comandir.png");
          width: 153px;
          height: 197px;
        }

        &-135 {
          background-image: url("../img/135.png");
          width: 192px;
          height: 158px;
        }

        &-v10 {
          background-image: url("../img/v10.png");
          width: 199px;
          height: 199px;
        }

        &-euro {
          background-image: url("../img/euro.png");
          width: 354px;
          height: 79px;
        }

        &-yandex {
          background-image: url("../img/yandex.png");
          width: 322px;
          height: 91px;
        }

        &-uber {
          background-image: url("../img/uber.png");
          width: 246px;
          height: 52px;
        }

        margin: 0 auto;
        margin-bottom: 40px;
        background-repeat: no-repeat;
        background-position: center;
        flex-grow: 1;
        background-size: contain;
        
        @media(max-width: 1500px) {
          width: 100%;
        }
      }
  }
}
