@font-face {
    font-family: "Proxima Nova";
    src: url("../fonts/ProximaNova-Bold.woff") format("woff");
    font-style: normal;
    font-weight: 400;
}

@font-face {
    font-family: "Proxima Nova - Black";
    src: url("../fonts/ProximaNova-Black.woff") format("woff");
    font-style: normal;
    font-weight: 400;
}
@font-face {
    font-family: "Proxima Nova - Semibold";
    src: url("../fonts/ProximaNova-Semibold.woff") format("woff");
    font-style: normal;
    font-weight: 400;
}
@font-face {
    font-family: "Proxima Nova - Extrabld";
    src: url("../fonts/ProximaNova-Extrabld.woff") format("woff");
    font-style: normal;
    font-weight: 400;
}

:root {
    --default: "Roboto" , sans-serif;
    --white: white;

}

*, ::after, ::before {
    box-sizing: border-box;
}

body, html {
    margin: 0;
    padding: 0;
    font-family: var(--default);
    font-size: 16px;
    font-weight: 400;
    line-height: 1.5;
    color: var(--black);
    background-color: var(--white);
}

.container {
    max-width: 1740px;
    margin: 0 auto;

    @media (max-width: 1740px) {
        width: 95%;
    }
}

.arrowup {
    position: fixed;
    right: 5%;
    bottom: 10%;
    border-radius: 50%;
    width: 48px;
    height: 48px;
    background: #2cbb25;;
    color: var(--white);
    font-size: 1.5rem;
    z-index: 3;
    cursor: pointer;
    text-align: center;
    line-height: 48px;
}