.what{
    
    padding: 200px 0;

    &__container {
        text-align: center;
        margin-bottom: 100px;
    }

    &__title {
        font-family: "Proxima Nova";
        font-size: 60px;
        font-weight: 700;
        color: #403f3f;
        letter-spacing: 3px;
        display: inline-block;
        position: relative;
        z-index: 10;

        @media(max-width: 1024px) {
            font-size: 30px;
        }
    }

    &__bg{
        width: 450px;
        height: 20px;
        background-color: #87d04c;
        position: relative;
        top: -37px;
        margin: 0 auto;
        transform: translateX(-23%);

        @media(max-width: 1024px) {
            display: none;
        }
    }

    &__wrapper {
        display: flex;
        justify-content: space-between;


        @media(max-width: 550px) {
            flex-wrap: wrap;
            justify-content: center;
        }

        &-item {
            width: 28%;

            @media(max-width: 768px) {
                width: 33%;
            }

            @media(max-width: 550px) {
                width: 90%;
                margin-bottom: 10px;
            }
        }

        &-icon {
            margin: 0 auto;

            @media(max-width: 1200px){
                transform: scale( .7);
            }

            @media(max-width: 1024px){
                transform: scale( .4);
            }
        }

        &-icon-mob {
            background-image: url('../img/svg/icon_mobile.svg');
            background-repeat: no-repeat;
            background-position: center;
            background-size: cover;
            width: 83px;
            height: 126px;
            margin-bottom: 25px;

            @media(max-width: 1024px){
                margin-top: 15px;
                margin-bottom: 10px;
            }
        }

        &-icon-mens {
            background-image: url('../img/svg/icon_mens.svg');
            background-repeat: no-repeat;
            background-position: center;
            background-size: contain;
            max-width: 260px;
            width: 100%;
            height: 146px;
        }

        &-icon-puzzle {
            background-image: url('../img/svg/icon_puzzle.svg');
            background-repeat: no-repeat;
            background-position: center;
            background-size: cover;
            width: 172px;
            height: 148px;
        }

        &-disc {
            font-family: "Proxima Nova";
            font-size: 30px;
            font-weight: 700;
            color: #414040;
            letter-spacing: 1.5px;
            text-align: center;
            padding-top: 25px;

            @media(max-width: 1200px){
                font-size: 18px;
            }

            @media(max-width: 1024px){
                padding-top: 0px;
            }
        }
    }

}