.footer {

  &__bottom {
    background-color: #959595;
    display: flex;
    justify-content: center;
    padding: 15px 0;

    &-item {
      color: #2a2a2a;
      font-family: "Proxima Nova - Semibold";
      font-size: 24px;
      font-weight: 400;
      line-height: 36px;
      text-transform: uppercase;
      text-decoration: none;

      @media(max-width: 1400px) {
        font-size: 16px;
      }

      a {
        color: #2a2a2a;
        font-family: "Proxima Nova - Semibold";
        font-size: 24px;
        font-weight: 400;
        line-height: 36px;
        text-transform: uppercase;
        text-decoration: none;
        padding: 0px 20px;
        transition: all .3s;

        &:hover {
          color: #1fca16;
        }


        @media(max-width: 1400px) {
          font-size: 16px;
        }
      }
    }
  }

  &__items {
    display: flex;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    text-align: center;
    position: relative;
    z-index: 2;

    @media(max-width: 1023px) {
        flex-wrap: wrap;
    }
  }

  &__top {
    background-color: #ebebeb;
    padding: 80px 0 150px;
    position: relative;
    overflow: hidden;

    &-item {
      @media(max-width: 1023px) {
        width: 100%;
      }
    }
    
    a {
      text-decoration: none;
      transition: all .3s;

      &:hover {
        color: #1fca16;
      }
    }

    &-logo {
      background: url(../img/logo_footer.png);
      width: 121px;
      height: 121px;
      margin: 0 auto;
      margin-bottom: 15px;
    }

    &-disc {
      color: #2a2a2a;
      font-family: "Proxima Nova - Semibold";
      font-size: 24px;
      font-weight: 400;
      line-height: 36px;
      text-transform: uppercase;
      text-decoration: none;

      @media(max-width: 1400px) {
        font-size: 16px;
      }

      a {
        color: #2a2a2a;
        font-family: "Proxima Nova - Semibold";
        font-size: 24px;
        font-weight: 400;
        line-height: 36px;
        text-transform: uppercase;
        text-decoration: none;
        transition: all .3s;

        &:hover {
          color: #1fca16;
        }

        @media(max-width: 1400px) {
          font-size: 16px;
        }
      }
    }
  }
}

#canvas {
	position: absolute;
	display: block;
	top: 0;
	left: 0;
	z-index: 1;
}