.otziv{
    
  padding: 200px 0;
  background-color: #dceffc;

  @media(max-width: 1024px) {
    padding: 50px 0;
  }

  &__container {
      text-align: center;
      margin-bottom: 100px;
  }

  &__title {
      font-family: "Proxima Nova";
      font-size: 60px;
      font-weight: 700;
      color: #403f3f;
      letter-spacing: 3px;
      display: inline-block;
      position: relative;
      z-index: 10;

      @media(max-width: 1024px) {
          font-size: 30px;
      }
  }

  &__bg{
      width: 240px;
      height: 20px;
      background-color: #87d04c;
      position: relative;
      top: -37px;
      margin: 0 auto;
      transform: translateX(-20%);

      @media(max-width: 1024px) {
          display: none;
      }
  }
}

.otziv__blocks {
  display: flex;
  

  @media(max-width: 950px) {
    flex-wrap: wrap-reverse;
  }
}

.otziv__block {
  width: 50%;
  text-align: center;
  justify-content: center;
  align-items: center;
  padding: 10px;

  @media(max-width: 1300px) {
    transform: scale(.7);
  }

  @media(max-width: 950px) {
    width: 100%;
  }

  &-caption {
    @media(max-width: 950px) {
      transform: scale(1);
    }
  }

  &-photo {
    background-image: url(../img/photo.png);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    width: 176px;
    height: 176px; 
    margin: 0 auto;
    margin-bottom: 50px;
    border: 10px solid white;
    border-radius: 50%;
    box-shadow: 0 0 10px rgba(0,0,0, .5);
  
  }

  &-title {
    color: #363535;
    font-family: "Proxima Nova - Semibold";
    font-size: 48px;
    font-weight: 400;
    margin-bottom: 50px;
    padding: 0 20px;
  }

  &-disc {
    color: #696969;
    font-family: "Proxima Nova";
    font-size: 24px;
    font-weight: 700;
    margin-bottom: 50px;

  }
  
  a {
    color: #036db8;
    /* color: #04b159; */
    font-family: "Proxima Nova - Semibold";
    font-size: 30px;
    font-weight: 400;
    text-decoration: none;
    border-bottom: 5px solid #036db8;
    padding-bottom: 10px;
  }
}

.card{

  &__photos {
    position: absolute;
    border: 10px solid white;
    border-radius: 50%;
    cursor: pointer;
    transition: all .3s;

    &:hover{
      border: 10px solid #adf870;
    }

    &-1 {
      top: 15px;
      left: 0;
    }

    &-2 {
      top: 100px;
      right: -40px;
    }

    &-3 {
      right: 10px;
      bottom: 15px;
    }

    &-4 {
      bottom: 0;
      left: 88px;
    }

    &-5 {
      bottom: 220px;
      left: -52px;
    }
  }


  &__border {
    width: 609px;
    height: 610px;
    border: 3px solid #c2c2c2;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    margin: 0 auto;


    @media(max-width: 650px) {
      transform: translate(-150px) scale(.5);
    }

  }

  &__green {
    background-color: #adf870;
    width: 406px;
    height: 406px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  &__photo {

    background-image: url(../img/mid-photo.png);
    background-repeat: no-repeat;
    background-position: center;
    width: 168px;
    height: 167px;
    margin: 0 auto;
    border: 10px solid white;
    border-radius: 50%;

  }
  

}