.friend {
  padding: 40px 0;

  &__items {
    display: flex;
    align-items: center;

    @media(max-width: 768px) {
      flex-wrap: wrap;
      flex-direction: column;
      align-content: center;
    }
  }

  &__item:nth-child(1) {
    background-image: url("../img/yandex.png");
    width: 322px;
    height: 91px;
  }

  &__item:nth-child(2) {
    background-image: url("../img/laim.png");
    width: 266px;
    height: 150px;
  }

  &__item:nth-child(3) {
    background-image: url("../img/iq.png");
    width: 293px;
    height: 131px;
  }

  &__item:nth-child(4) {
    background-image: url("../img/euro.png");
    width: 354px;
    height: 79px;
  }

  &__item {
    margin: 0 auto;
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    opacity: .5;
    transition: all .3s;

    @media(max-width: 768px) {
      
      margin: 20px 0;
      transform: scale(.5);
    }

    &:hover {
      opacity: 1;
    }


  }
}